.logo {
    font-family: 'Oleo Script', cursive;
    background: #ff4486;
    color: #fff;
    border-radius: $border-radius;
    font-weight: 400;
    padding: 0.25em 0.2em 0.25em 0.2em;
    font-size: 3.25em;
    letter-spacing: 0.05em;
    display: inline-block;
    vertical-align: middle;
    text-decoration: none;
}